import React, { PureComponent } from "react";
import "../../common/Thumbnail/style.scss";
import { getFile, os } from "../../util/util";
import { inject, observer } from "mobx-react";

@inject("RecycleBin")
@observer
class Thumbnail extends PureComponent {
  state = {};
  render() {
    let isSelected;
    const {
      style,
      index,
      onBigImg,
      item,
      RecycleBin: { selectItems, selectList },
    } = this.props;
    if (item) {
      isSelected = selectItems.has(item.id);
    }

    try {
      if (item.true) {
        return <div style={style}></div>;
      } else {
        return (
          <div
            className={`thumbnail ${isSelected ? "selected" : ""}`}
            style={{
              ...style,
              background: `#d3d3d3 url(${getFile.file_s(
                item.id
              )}) center center no-repeat`,
              backgroundSize:'cover',
            }}
            ref={this.imgRef}
            onClick={(e) => {
              if (selectItems.size) {
                selectList(e, item.id);
              } else {
                onBigImg(index);
              }
            }}
          >
            <span
              style={
                os().isPhone || os().isAndroid || os().isTablet
                  ? { visibility: "inherit" }
                  : {}
              }
              className="icon-check-s treeNodeUnselectable"
              onClick={(e) => {
                selectList(e, item.id);
              }}
            >
              <i
                className="iconfont"
                style={isSelected ? {} : { visibility: "hidden" }}
              >
                &#xe62d;
              </i>
            </span>
            {item.ext === "gif" && <i className="iconfont GIF-IMG">&#xe670;</i>}
            {/(avi|mov|rmvb|rm|flv|mp4|3gb)$/.test(item.ext) && (
              <i className="iconfont v_icon">&#xe6ae;</i>
            )}
          </div>
        );
      }
    } catch (error) {
      return <div style={style}></div>;
    }
  }
}

export default Thumbnail;
