import React, { Component } from "react";
import AutoSizer from "react-virtualized-auto-sizer";
import { FixedSizeGrid as Grid } from "react-window";
import Goblack from "../../common/Goblack";
import "./style.scss";
import Operation from "../../common/Operation";
import { wsget } from "../../ws";
import Loading from "../../common/Widget/Loading";
import Nodata from "../../common/Widget/Nodata";
import Thumbnail from "./Thumbnail";
import { inject, observer } from "mobx-react";
import { withTranslation } from "react-i18next";
import { Modal, message } from "antd";
import PlaylistCell from "../../common/PlaylistCell";
import BigimgCpt from "./BigImg";
import { imgLoad, getFile, selfAdaption } from "../../util/util";
import { observable } from "mobx";

@withTranslation()
@inject("RecycleBin")
@observer
class ClearPhoto extends Component {
  constructor(props) {
    super(props);
    this.imgRef = React.createRef();
    this.playlistInner = React.createRef();
    this.state = {
      inint: false,
      list: [],
      realNum: 0,
      rowCount: 0,
      toItems: [{ name: "Playlist" }, { name: "Alubm" }],
      visible: false,
      toPlaceInint: false,
      toPlaceData: [],
      seleceds: new Set(),
      bImg_visible: false,
      imgfile: {},
      listNum: 0,
    };
  }
  @observable inner = false;

  componentDidMount() {
    this.resize();
    window.addEventListener("resize", this.resize);
    wsget("file_unused", null).then((data) => {
      if (!this.colse) {
        this.util(data);
      }
    });
  }

  componentWillUnmount() {
    this.props.RecycleBin.reset();
    this.colse = true;
    window.removeEventListener("resize", this.resize);
  }

  resize = () => {
    const { list, toPlaceData } = this.state;
    this.getWH();
    this.rowCount = Math.ceil(list.length / this.columnCount);
    if (this.state.visible) {
      this.adaption();
      this.listRowNum = toPlaceData.length / this.listColumnNum;
    }

    if (window.innerWidth < 1000) {
      if (!this.inner) {
        this.inner = true;
      }
    } else {
      if (this.inner) {
        this.inner = false;
      }
    }
  };

  util = (data) => {
    const num = data.length;
    this.getWH();
    this.rowCount = data.length / this.columnCount;
    if (data.length % this.columnCount !== 0) {
      const leng = this.columnCount - (data.length % this.columnCount);
      for (let index = 0; index < leng; index++) {
        data.push({ true: true });
      }
    }
    this.setState({
      list: data,
      inint: true,
      realNum: num,
    });
  };

  //图片计算宽高
  getWH = (data) => {
    try {
      const { wi, hi, colNum } = selfAdaption(
        this.imgRef.current.offsetWidth,
        165,
        150,
        100,
        100,
        597
      );
      this.childW = wi;
      this.childH = hi;
      this.columnCount = colNum;
    } catch (error) {
      console.log(error);
      this.props.history.push("/");
    }
  };

  adaption = () => {
    let Width = this.playlistInner.current.offsetWidth;
    const { wi, hi, colNum } = selfAdaption(Width, 200, 210, 180, 190);
    this.PchildW = wi;
    this.PchildH = hi;
    this.listColumnNum = colNum;
  };

  util2 = (data, album) => {
    const num = data.length;
    this.adaption();
    this.listRowNum = data.length / this.listColumnNum;
    if (data.length % this.listColumnNum !== 0) {
      const leng = this.listColumnNum - (data.length % this.listColumnNum);
      for (let index = 0; index < leng; index++) {
        data.push({ true: true });
      }
    }
    this.setState({
      toPlaceData: data,
      toPlaceInint: true,
      listNum: num,
      album,
    });
  };

  toListRender = ({ columnIndex, data, rowIndex, style }) => {
    const { album } = this.state;
    const index = rowIndex * this.listColumnNum + columnIndex;
    const item = data[index];
    return (
      <PlaylistCell
        key={item.id}
        style={style}
        item={item}
        goPlaylistdetails={this.props.RecycleBin.goPlaylistdetails}
        seleceds={this.props.RecycleBin.seleceds}
        // isSelected={this.props.RecycleBin.seleceds.has(item.id)}
        album={album}
      />
    );
  };

  onBigImg = (i) => {
    const { realNum, list } = this.state;
    const img = list[i];
    this.imgidx = i;
    this.setState({ bImg_visible: true, imgfile: img });
    if (!/(avi|mov|rmvb|rm|flv|mp4|3gb)$/.test(list[i].ext)) {
      if (i + 1 < realNum) {
        if (!/(avi|mov|rmvb|rm|flv|mp4|3gb)$/.test(list[i + 1].ext)) {
          imgLoad(getFile.file_b(list[i + 1].id));
        }
      }
      if (!(i - 1 < 0)) {
        if (!/(avi|mov|rmvb|rm|flv|mp4|3gb)$/.test(list[i - 1].ext)) {
          imgLoad(getFile.file_b(list[i - 1].id));
        }
      }
    } else {
      if (i + 1 < realNum) {
        if (!/(avi|mov|rmvb|rm|flv|mp4|3gb)$/.test(list[i + 1].ext)) {
          imgLoad(getFile.file_b(list[i + 1].id));
        }
      }
      if (i - 1 >= 0) {
        if (!/(avi|mov|rmvb|rm|flv|mp4|3gb)$/.test(list[i - 1].ext)) {
          imgLoad(getFile.file_b(list[i - 1].id));
        }
      }
    }
  };

  onPiece = () => {
    if (this.imgidx - 1 >= 0) {
      this.onBigImg(this.imgidx - 1);
    } else {
      this.imgidx = this.state.realNum - 1;
      this.onBigImg(this.imgidx);
      message.warning(this.props.t("lastOne"));
    }
  };

  nextPage = () => {
    if (this.imgidx + 1 >= this.state.realNum) {
      this.imgidx = 0;
      this.onBigImg(this.imgidx);
      message.warning(this.props.t("firstOne"));
      return;
    }
    this.onBigImg(this.imgidx + 1);
  };

  closeDialog = () => {
    this.setState({
      bImg_visible: false,
    });
  };

  thumbnail = ({ columnIndex, data, rowIndex, style }) => {
    const index = rowIndex * this.columnCount + columnIndex;
    const item = data[index];
    return (
      <Thumbnail
        index={index}
        item={item}
        style={style}
        onBigImg={this.onBigImg}
      />
    );
  };

  toPlaceClick = ({ key }) => {
    this.adaption();
    this.setState({ visible: true });
    if (key === "0") {
      wsget("playlist_list", null).then((data) => {
        this.util2(data, false);
        // this.setState({ toPlaceData: data, toPlaceInint: true, album: false })
      });
    } else if (key === "1") {
      wsget("album_list", null).then((data) => {
        this.util2(data, true);
        // this.setState({ toPlaceData: data, toPlaceInint: true, album: true })
      });
    }
  };

  handleCancel = () => {
    this.setState({
      visible: false,
      confirmLoading: false,
      toPlaceInint: false,
    });
    this.props.RecycleBin.ToPlaceDelete();
  };

  remove = () => {
    Modal.confirm({
      title: "Are you sure to delete these files?",
      content: "Deletion cannot be restored",
      onOk: () => {
        const list = this.state.list.filter((el) => el.id);
        const arr = Array.from(this.props.RecycleBin.selectItems);
        wsget("file_delete_unused", arr, 1).then((res) => {
          if (!this.colse) {
            arr.forEach((item) => {
              const pos = list.findIndex((el) => el.id === item);
              list.splice(pos, 1);
            });
            this.util(list);
            this.props.RecycleBin.delete();
          }
        });
      },
      onCancel() {
        console.log("Cancel");
      },
    });
  };

  handleOk = () => {
    let arr = [];
    let list = [];
    const initList = this.state.list;
    const { album } = this.state;
    const { selectItems, seleceds } = this.props.RecycleBin;
    this.setState({
      confirmLoading: true,
    });
    Array.from(selectItems).forEach((id) => {
      list.push({ id });
    });
    Array.from(seleceds).forEach((item) => {
      if (album) {
        const obj = {
          id: item,
          list,
        };
        arr.push(wsget("album_add_files", obj));
      } else {
        const obj = {
          id: item,
          list,
          index: 0,
        };
        arr.push(wsget("playlist_save_to", obj));
      }
    });

    Promise.all(arr).finally(() => {
      message.success(this.props.t("successful"));
      Array.from(selectItems).forEach((id) => {
        const pos = initList.findIndex((el) => el.id === id);
        initList.splice(pos, 1);
      });
      this.setState({
        visible: false,
        confirmLoading: false,
        // seleceds: new Set(),
        list: initList,
      });
      this.props.RecycleBin.delete();
      this.props.RecycleBin.ToPlaceDelete();
    });
  };

  goblack = () => {
    this.props.history.push(`/`);
  };

  render() {
    const {
      inint,
      list,
      realNum,
      toItems,
      visible,
      confirmLoading,
      toPlaceInint,
      toPlaceData,
      album,
      bImg_visible,
      imgfile,
      listNum,
    } = this.state;
    const {
      selectItems,
      checkAll,
      invert,
      ToPlaceCheckAll,
      seleceds,
      ToPlaceInvert,
      cancelAll,
    } = this.props.RecycleBin;
    return (
      <div className="ClearPhoto">
        <div className="clear_header">
          <Goblack go={this.goblack} name={this.props.t("temporaryFiles")} />
        </div>
        <Operation
          selectNum={selectItems.size}
          dataNum={realNum}
          oncheck={() => {
            checkAll(list);
          }}
          invert={() => {
            invert(list);
          }}
          remove={this.remove}
          toPlace={toItems}
          toPlaceClick={this.toPlaceClick}
          cancelAll={cancelAll}
        />
        <div ref={this.imgRef} className="imglist_box">
          {inint ? (
            list.length > 0 ? (
              <AutoSizer>
                {({ height, width }) => {
                  return (
                    <Grid
                      className="Grid"
                      itemData={list}
                      rowCount={this.rowCount}
                      columnCount={this.columnCount}
                      columnWidth={this.childW}
                      height={height}
                      rowHeight={this.childH}
                      width={width}
                    >
                      {this.thumbnail}
                    </Grid>
                  );
                }}
              </AutoSizer>
            ) : (
              <Nodata />
            )
          ) : (
            <Loading />
          )}
        </div>

        <Modal
          title={album ? "Albums" : "Playlists"}
          visible={visible}
          onOk={this.handleOk}
          confirmLoading={confirmLoading}
          onCancel={this.handleCancel}
          width={this.inner ? "100%" : 1000}
          getContainer={false}
          centered
        >
          <div className="ToPlace-inner">
            {toPlaceInint && (
              <Operation
                selectNum={seleceds.size}
                dataNum={listNum}
                oncheck={() => {
                  ToPlaceCheckAll(toPlaceData);
                }}
                invert={() => {
                  ToPlaceInvert(toPlaceData);
                }}
              />
            )}
            <div className="list-inner" ref={this.playlistInner}>
              {toPlaceInint ? (
                toPlaceData.length > 0 ? (
                  <AutoSizer>
                    {({ height, width }) => {
                      return (
                        <Grid
                          className="Grid"
                          itemData={toPlaceData}
                          rowCount={this.listRowNum}
                          columnCount={this.listColumnNum}
                          columnWidth={this.PchildW}
                          height={height}
                          rowHeight={this.PchildH}
                          width={width}
                        >
                          {this.toListRender}
                        </Grid>
                      );
                    }}
                  </AutoSizer>
                ) : (
                  <Nodata />
                )
              ) : (
                <Loading />
              )}
            </div>
          </div>
        </Modal>

        {bImg_visible && (
          <BigimgCpt
            closeDialog={this.closeDialog}
            imgfile={imgfile}
            onPiece={this.onPiece}
            nextPage={this.nextPage}
          />
        )}
      </div>
    );
  }
}

export default ClearPhoto;
