import React, { PureComponent } from "react";
import { Tooltip } from "antd";
import { withTranslation } from "react-i18next";
import LazyImage from "./Lazy-image";
import imgload2 from "../images/imgerror.png";
import { getFile, os } from "../util/util";
import dayjs from "dayjs";
import { observer } from "mobx-react";
import "./index.scss";

@withTranslation()
@observer
class PlaylistCell extends PureComponent {
  render() {
    let isSelected;
    const { t, item, style, goPlaylistdetails, album, seleceds } = this.props;
    if (item && seleceds) {
      isSelected = seleceds.has(item.id);
    }
    try {
      if (item.true) {
        return <div style={style}></div>;
      } else {
        return (
          <div className="PlaylistCell" style={style}>
            <div
              className={`list_cell ${isSelected ? "list_cell_check" : ""}`}
              onClick={() => {
                goPlaylistdetails &&
                  goPlaylistdetails(item.id, item.name, item.created);
              }}
            >
              <span
               style={
                os().isPhone || os().isAndroid || os().isTablet
                  ? { visibility: "inherit" }
                  : {}
              }
                className={`icon-check-s treeNodeUnselectable`}
                onClick={(e) => {}}
              >
                <i
                  className="iconfont"
                  style={isSelected ? {} : { visibility: "hidden" }}
                >
                  &#xe62d;
                </i>
              </span>
              <div className="top_img">
                {album ? (
                  <div
                    className="album_top_img"
                    style={
                      item.profile
                        ? {}
                        : {
                            backgroundImage: `url(${imgload2})`,
                            backgroundSize: "100% 100%",
                          }
                    }
                  >
                    {item.profile && (
                      <LazyImage
                        src={getFile.file_s(item.profile.id)}
                        alt={item.profile.id}
                      />
                    )}
                  </div>
                ) : (
                  <>
                    <div
                      className="top_topImg"
                      style={
                        item.profile
                          ? {}
                          : {
                              backgroundImage: `url(${imgload2})`,
                              backgroundSize: "100% 140%",
                            }
                      }
                    >
                      {item.profile && (
                        <LazyImage
                          src={getFile.file_s(item.profile.id)}
                          alt={item.profile.id}
                        />
                      )}
                    </div>
                    <div className="top_bottomImgs">
                      <span
                        className="img1"
                        style={
                          item.profile_1
                            ? {}
                            : {
                                backgroundImage: `url(${imgload2})`,
                                backgroundSize: "100% 100%",
                              }
                        }
                      >
                        {item.profile_1 && (
                          <LazyImage
                            src={getFile.file_s(item.profile_1.id)}
                            alt={item.profile_1.id}
                          />
                        )}
                      </span>
                      <span
                        className="img2"
                        style={
                          item.profile_2
                            ? {}
                            : {
                                backgroundImage: `url(${imgload2})`,
                                backgroundSize: "100% 100%",
                              }
                        }
                      >
                        {item.profile_2 && (
                          <LazyImage
                            src={getFile.file_s(item.profile_2.id)}
                            alt={item.profile_2.id}
                          />
                        )}
                      </span>
                      <span
                        className="img3"
                        style={
                          item.profile_3
                            ? {}
                            : {
                                backgroundImage: `url(${imgload2})`,
                                backgroundSize: "100% 100%",
                              }
                        }
                      >
                        {item.profile_3 && (
                          <LazyImage
                            src={getFile.file_s(item.profile_3.id)}
                            alt={item.profile_3.id}
                          />
                        )}
                      </span>
                    </div>
                  </>
                )}
              </div>
              <div className="information">
                <div className="information_top">
                  <Tooltip
                    placement="bottom"
                    overlayClassName="tooltip"
                    title={item.name}
                  >
                    <p className="name ellipsis">{item.name}</p>
                  </Tooltip>
                  <p className="time">
                    <span className="time-span">
                      {t("LastUpdate")}
                      {dayjs(item.updated || item.created).format(t("L"))}
                    </span>
                  </p>
                  {item.frame_default > 0 && (
                    <span className="frames_def">{t("default")}</span>
                  )}
                </div>
                <div className="information_bottom">
                  <span style={{ marginRight: "10px" }}>
                    {item.filecount ? item.filecount : 0} {t("Photo")}
                  </span>
                  {!album && (
                    <span>
                      {item.frame_count} {t("photoframe")}
                    </span>
                  )}
                </div>
              </div>
            </div>
          </div>
        );
      }
    } catch (error) {
      return <div style={style}></div>;
    }
  }
}

export default PlaylistCell;
